@import "~frontend-scss-mixins/mixins";

@mixin font200 {
  font-weight: 200;
  font-style: italic;
}

@mixin font400 {
  font-weight: 400;
}

@mixin font500 {
  font-weight: 500;
}

@mixin font700 {
  font-weight: 700;
}

@mixin active-button-effect {

  &:after {
    @include pseudo-absolute;
    @include absolute-fill;
    background: var(--light-main-color);
    z-index: -1;
    transform: scale(0.9);
    border-radius: 4px;
  }

  &:active:after {
    transform: scaleX(1.1) scaleY(1.3);
    opacity: 0;
  }

}

@mixin active-disabled-button-effect($border-radius:4px) {

  &:after {
    @include pseudo-absolute;
    @include absolute-fill;
    background: var(--error-color);
    z-index: -1;
    transform: scale(0.9);
    border-radius: #{$border-radius};
  }

  &:active:after {
    transform: scaleX(1.1) scaleY(1.3);
    opacity: 0;
  }
}

@mixin input-placeholder {
  &::-webkit-input-placeholder {
    @content
  }
  &::-moz-placeholder {
    @content
  }
  &:-moz-placeholder {
    @content
  }
  &:-ms-input-placeholder {
    @content
  }
}

@mixin customPreloader($color,$side:40,$speed:3,$z-index:auto) {
  $image-background: 'data:image/svg+xml;base64,';

  position: relative;

  &:before {
    display: block;
    content: '';
    width: #{$side}px;
    height: #{$side}px;
    position: absolute;
    background: $color;
    left: calc(50% - #{$side / 2}px);
    top: calc(50% - #{$side / 2}px);
    -webkit-mask: url('#{$image-background}');
    mask: url('#{$image-background}');
    mask-repeat: no-repeat;
    mask-size: contain;
    mask-position: center center;
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-size: contain;
    -webkit-mask-position: center center;
    z-index: $z-index;

    @keyframes rotate {
      0% {
        transform: rotate(0);
      }
      50% {
        transform: rotate(180deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }

    animation: rotate #{$speed}s linear;
    animation-iteration-count: infinite;

  }
}

@mixin customVerticalScroll() {
  @include vertical-scroll(
                  10,
                  var(--extra-light-natural-color),
                  var(--light-main-color),
                  var(--main-color),
  );

  &::-webkit-scrollbar-thumb {
    cursor: pointer !important;
  }
}

@mixin milago {
  font-family: 'Milago', sans-serif;
}

@mixin manrope {
  font-family: 'Manrope', sans-serif;
}