@import "~frontend-scss-mixins/mixins";
@import "../base/mixins";
@import "../base/variables";
@import "../base/media-queries";

.clients {
  background: var(--black-color);
  color: var(--white-color);
  padding-top: 9vw;
  padding-bottom: 25vw;

  @include tablet-big-screen {
    padding-top: 33vw;
    padding-bottom: 81vw;
  }

  &__text-row {
    margin-bottom: 8.6vw;
    position: relative;
    height: 2vw;
  }

  &__text-row-line {
    @include flex-row;
    width: fit-content;
    left: 0;
    top: 0;
    opacity: 0.3;
    position: absolute;
    animation: scroll1 25s infinite linear;

    &--2 {
      transform: translateX(100%);
      animation: scroll2 25s infinite linear;
    }
  }

  @keyframes scroll1 {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-100%);
    }
  }

  @keyframes scroll2 {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(0);
    }
  }

  &__text-row-item {
    @include manrope;
    font-weight: 300;
    font-size: 3.4392vw;
    line-height: 100%;
    letter-spacing: -0.033em;
    margin-right: 3.8vw;
    position: relative;

    @include tablet-big-screen {
      font-size: 19vw;
    }

    &:after {
      @include pseudo-absolute;
      width: .3vw;
      height: .3vw;
      border-radius: 50%;
      background: var(--white-color);
      top: 2vw;
      right: -2.2vw;
    }
  }

  &__block {
    width: 100%;
    height: 51vw;
    position: relative;

    @include tablet-big-screen {
      margin-top: 55vw;
      height: 240vw;
    }
  }

  &__title {
    @include manrope;
    font-weight: 700;
    font-size: 1.7196vw;
    line-height: 100%;
    letter-spacing: -0.033em;
    width: 7vw;

    @include tablet-big-screen {
      font-size: 9vw;
    }
  }

  &__item {
    border-radius: 50%;
    border: .02vw solid var(--main-color);
    position: absolute;
    @include flex-row-center;

    &--main {
      background: var(--main-color);
    }

    &--1 {
      left: 1vw;
      top: 12.2vw;
      height: 12vw;
      width: 12vw;

      @include tablet-big-screen {
        left: -8vw;
        top: 157vw;
        height: 38vw;
        width: 38vw;
      }
    }

    &--2 {
      left: 27.5vw;
      top: 6.9vw;
      height: 7vw;
      width: 7vw;

      @include tablet-big-screen {
        display: none;
      }
    }

    &--3 {
      left: 64.1vw;
      top: 0.2vw;
      height: 15.4vw;
      width: 15.4vw;


      @include tablet-big-screen {
        left: 64vw;
        top: 15vw;
        height: 37vw;
        width: 37vw;
      }
    }

    &--4 {
      left: 10.9vw;
      top: 23.2vw;
      height: 27.8vw;
      width: 27.8vw;

      @include tablet-big-screen {
        left: -25vw;
        top: 44vw;
        height: 99vw;
        width: 99vw;
      }
    }

    &--5 {
      left: 43.5vw;
      top: 12.9vw;
      height: 18.8vw;
      width: 18.8vw;

      @include tablet-big-screen {
        left: 50vw;
        top: 139vw;
        height: 74vw;
        width: 74vw;
      }
    }

    &--6 {
      left: 73.9vw;
      top: 22.7vw;
      height: 12.1vw;
      width: 12.1vw;

      @include tablet-big-screen {
        left: 5vw;
        top: 209vw;
        height: 48vw;
        width: 48vw;
      }
    }

    &--7 {
      left: 57.9vw;
      top: 37.2vw;
      height: 12.1vw;
      width: 12.1vw;

      @include tablet-big-screen {
        display: none;
      }
    }
  }

  &__item-img {
    display: block;
    height: auto;

    &--gs {
      width: 3.4392vw;

      @include tablet-big-screen {
        width: 12vw;
      }
    }

    &--creo {
      width: 2.8139vw;
    }

    &--0-100 {
      width: 7.7644vw;

      @include tablet-big-screen {
        width: 17vw;
      }
    }

    &--traffic {
      width: 8.3897vw;

      @include tablet-big-screen {
        width: 28vw;
      }
    }

    &--chart {
      width: 2.6576vw;

      @include tablet-big-screen {
        width: 13vw;
      }
    }

    &--vooodoo {
      width: 5.2110vw;

      @include tablet-big-screen {
        width: 26vw;
      }
    }

    &--other {
      width: 4.8983vw;
    }
  }
}

